/**
 * Conf file
 */

export default class Conf {
    CONF = {
        //  []
        "requestDevelopmentName": "",

        //  []
        "requestProductionName": "",

        // 
        "child": {
        }
    };

    // 
    async initConf() {
        let confs = await import('/public/conf/requestConf.json');
        this.CONF = Object.assign(
            this.CONF,
            {...confs}
        );
        return this.CONF;
    }

    get getConf() {
        return this.CONF;
    }
}
