import conf from '../../../package.json';

export default class Print {
    styles = [
        'colorgreen',
        'backgroundyellow',
        'font-size30px',
        'border1px solid red',
        'text-shadow2px 2px black',
        'padding10px',
    ];

    log (v) {
        const that = this;
        if ( (process.env.NODE_ENV || 'development') == 'development') return;
        console.log('%c' + v, that.styles.join(';') );
    }
}